'use client';

import { firebaseConfig } from '@/firebase/config';
import { initializeApp } from 'firebase/app';
import { getFirestore, doc, getDoc } from 'firebase/firestore';
import { useState } from 'react';

// This is a self-contained test component.
// It does not use any of the project's existing Firebase hooks or providers.

export default function FirebaseTestPage() {
  const [log, setLog] = useState<string[]>([]);
  const [isLoading, setIsLoading] = useState(false);

  const addLog = (message: string) => {
    console.log(message);
    setLog(prev => [...prev, `${new Date().toLocaleTimeString()}: ${message}`]);
  };

  const runTest = async () => {
    setIsLoading(true);
    addLog('Starting test...');

    try {
      addLog('Step 1: Initializing Firebase App...');
      const app = initializeApp(firebaseConfig);
      addLog('Firebase App initialized successfully.');

      addLog('Step 2: Getting Firestore instance...');
      const db = getFirestore(app);
      addLog('Firestore instance obtained successfully.');

      addLog('Step 3: Defining document reference to /test/test...');
      const testDocRef = doc(db, 'test', 'test');
      addLog('Document reference created.');

      addLog('Step 4: Attempting to get document snapshot...');
      // This is the step that is failing in the main application.
      const docSnap = await getDoc(testDocRef);
      addLog('getDoc() promise resolved.');

      if (docSnap.exists()) {
        addLog('SUCCESS: Test document exists. Firestore connection is working.');
      } else {
        addLog('SUCCESS: Test document does not exist, but the read was successful. Firestore connection is working.');
      }
    } catch (error: any) {
      addLog(`ERROR: Test failed. Catched error.`);
      addLog(`Error Code: ${error.code}`);
      addLog(`Error Message: ${error.message}`);
      addLog(`Full Error: ${JSON.stringify(error)}`);
    } finally {
      setIsLoading(false);
      addLog('Test finished.');
    }
  };

  return (
    <div style={{ padding: '20px', fontFamily: 'monospace' }}>
      <h1>Firestore Connection Test</h1>
      <p>
        This page attempts to connect to Firestore in isolation. Click the button to run the test.
        This will tell us if the Firestore SDK is fundamentally able to connect in this environment.
      </p>
      <button onClick={runTest} disabled={isLoading} style={{ padding: '10px', fontSize: '16px' }}>
        {isLoading ? 'Running Test...' : 'Run Firestore Connection Test'}
      </button>
      <h2>Log:</h2>
      <pre style={{ background: '#f0f0f0', padding: '10px', border: '1px solid #ccc' }}>
        {log.join('\n')}
      </pre>
    </div>
  );
}
